<template>
  <div class="app-wrapper">
    <layout-header :isShowNavbar="isShowNavbar" />
    <layout-navbar :isShowNavbar="isShowNavbar" />
    <app-main />
  </div>
</template>

<script>
import LayoutHeader from './components/LayoutHeader.vue'
import LayoutNavbar from './components/LayoutNavbar.vue'
import AppMain from './components/AppMain.vue'

export default {
  name: 'Layout',
  components: { LayoutNavbar, LayoutHeader, AppMain },
  data() {
    return {
      isShowNavbar: false
    }
  },
  created() {
    this.$Bus.$on('changeNavbarVisible', () => {
      this.isShowNavbar = !this.isShowNavbar
    })
  }
}
</script>
