import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

/* Layout */
import Layout from '@/layout'

const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    children: [
      {
        path: 'dashboard',
        component: () => import('@/views/dashboard/index.vue'),
        name: 'Dashboard',
        meta: {
          icon: 'dashboard',
          title: '故障快速应急平台',
          navbarTitle: '首页'
        }
      }
    ]
  },
  {
    path: '/library',
    component: Layout,
    redirect: '/library/index',
    children: [
      {
        path: 'index',
        component: () => import('@/views/library/index.vue'),
        name: 'Library',
        meta: { icon: 'library', title: '图书馆' }
      }
    ]
  },
  {
    path: '/duxiu',
    component: Layout,
    redirect: '/duxiu/index',
    children: [
      {
        path: 'index',
        component: () => import('@/views/duxiu/index.vue'),
        name: 'Duxiu',
        meta: {
          icon: 'duxiu',
          title: '读秀',
          navbarTitle: '读秀nginx'
        }
      }
    ]
  },
  {
    path: '/services',
    component: Layout,
    redirect: '/services/services',
    name: 'Services',
    meta: { icon: 'services', title: '服务管理' },
    children: [
      {
        path: 'services',
        component: () => import('@/views/services/services.vue'),
        name: 'ServicesManagement',
        meta: { title: '服务管理' }
      },
      {
        path: 'application',
        component: () => import('@/views/services/application.vue'),
        name: 'ApplicationManagement',
        meta: { title: '应用类型管理' }
      },
      {
        path: 'operation',
        component: () => import('@/views/services/operation.vue'),
        name: 'OperationManagement',
        meta: { title: '操作类型管理' }
      }
    ]
  },
  {
    path: '/system',
    component: Layout,
    redirect: '/system/user',
    name: 'System',
    meta: { icon: 'system', title: '系统管理' },
    children: [
      {
        path: 'user',
        component: () => import('@/views/system/user.vue'),
        name: 'UserManagement',
        meta: { title: '用户管理' }
      },
      {
        path: 'log',
        component: () => import('@/views/system/log.vue'),
        name: 'LogManagement',
        meta: { title: '日志管理' }
      }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
